import topSearch from "@/components/topSearch.vue";
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import inquireList from "@/components/inquireList-select.vue";

let _this,timer
export default {
	components:{
		topSearch,
		uniLoadMore,
		inquireList
	},
	data() {
		return {
		    loadingType: 'more',
			customerList:[],
			keyword:'',
			pageNum: 1,
			pageSize: 20,
			isLastPage:false
		};
	},
	onReachBottom: function () {
	  if (timer != null) {
	             clearTimeout(timer);
	         }
	    timer = setTimeout(function() {
	         _this.getMoreCustomer();
	     }, 1000);
	  
	
	 },
	 onPullDownRefresh: function () {
		_this.getCustomerList();
	 },
	onLoad:function(){
		_this = this
		this.getCustomList()
	},
	methods:{
		async getMoreCustomer(){
			    if (_this.loadingType !== 'more') {//loadingType!='more';直接返回
			    	return false;
			    }
				_this.loadingType = 'loading';
				uni.showNavigationBarLoading();//显示加载动画
				
			    let data= {
					keyword: this.keyword,  //	搜索关键字
					pageNum: this.pageNum,		   //当前页数
					pageSize: this.pageSize
				}
			    const res = await this.$http.get('/latent/inquiry_list',{data:data})
				 console.log(res)
				  if (res.data.data.list.length ==0){
					  _this.loadingType =''
					  uni.hideNavigationBarLoading();//关闭加载动画
					  return
				  }
				   
				 _this.pageNum++;//每触底一次 page +1
				  console.log(res)
				  _this.customerList = _this.customerList.concat(res.data.data.list)
				 _this.loadingType = 'more';//将loadingType归0重置
				 uni.hideNavigationBarLoading();//关闭加载动画
				 
			   
		},
		async handleSearch(e){
			console.log(e)
			this.keyword = e
			let url =  this.Api.buyerList
			let data ={
				keyword: this.keyword,  //	搜索关键字
				pageNum: 1,		   //当前页数
				pageSize: this.pageSize
			}
			const res = await this.$http.get('/latent/inquiry_list',{data:data})
			 console.log(res);
			_this.customerList = res.data.data.list
		},
		async getCustomList(){
			_this.pageNum = 1
			_this.loadingType = 'more';
			uni.showNavigationBarLoading();
			
			let url =  this.Api.buyerList
			let data ={
				keyword: this.keyword,  //	搜索关键字
				pageNum: this.pageNum,		   //当前页数
				pageSize: this.pageSize,
				
			}
			const res = await this.$http.get('/latent/inquiry_list',{data:data})
			 _this.pageNum++;
			  console.log(res);
			 _this.customerList = res.data.data.list
			 
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();//得到数据后停止下拉刷新
		},
		toDetail:function(data){
			console.log(data[0],data[1])
			var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				// prevPage.setData({
				// 		buyer:  realName,
				// 		buyerCode: code
				// })
				prevPage.$vm.form.inquiryNumber = data[0]
				prevPage.$vm.tradeName = data[1]
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
